 12px 12px 12px 0;\n\tmin-height: 69px;\n}\n\n.new-message-form {\n\talign-items: flex-end;\n\tdisplay: flex;\n\tgap: 4px;\n\tposition: relative;\n\tmax-width: $message-input-max-width;\n\tmargin: 0 auto;\n\n\t&__emoji-picker {\n\t\tposition: absolute;\n\t\tbottom: 0;\n\t\tz-index: 1;\n\t}\n\n\t&__input {\n\t\tflex-grow: 1;\n\t\tposition: relative;\n\t\tmin-width: 0;\n\t}\n\n\t// Override NcRichContenteditable styles\n\t:deep(.rich-contenteditable__input) {\n\t\tborder-radius: calc(var(--default-clickable-area) / 2);\n\t\tpadding: 8px 16px 8px 44px;\n\t\tmax-height: 180px;\n\t}\n\n\t&__quote {\n\t\tmargin: 0 16px 12px;\n\t\tbackground-color: var(--color-background-hover);\n\t\tpadding: 8px;\n\t\tborder-radius: var(--border-radius-large);\n\t}\n\n\t// put a grey round background when popover is opened or hover-focused\n\t&__icon:hover,\n\t&__icon:focus,\n\t&__icon:active {\n\t\topacity: 1;\n\t\t// good looking on dark AND white bg\n\t\tbackground-color: rgba(127, 127, 127, .25);\n\t}\n}\n\n// Override actions styles TODO: upstream this change\n// Targeting two classes for specificity\n:deep(.action-item__menutoggle.action-item__menutoggle--with-icon-slot) {\n\topacity: 1 !important;\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: var(--color-background-hover) !important;\n\t}\n\n\t&:disabled {\n\t\topacity: .5 !important;\n\t}\n}\n\n// Hardcode to prevent RTL affecting on user mentions\n:deep(.mention-bubble) {\n\tdirection: ltr;\n}\n","/**\n * @copyright Copyright (c) 2019 John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @author John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @license AGPL-3.0-or-later\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message utils width\n$message-utils-width: 100px;\n\n$message-input-max-width: calc($messages-list-max-width - 100px);\n\n// background color of call container\n$color-call-background: rgba(34, 34, 34, 0.8);\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
