o,\n\t&-leave {\n\t\ttransform: translateY(0);\n\t\topacity: 1;\n\t}\n\t&-enter-active,\n\t&-leave-active {\n\t\tpointer-events: none;\n\t\ttransition: $transition-slow;\n\t\ttransition-property: transform, opacity;\n\t}\n}\n\n// TODO manipulate with transform: scaleX() instead of width\n@mixin slide-right-rules {\n\t&-enter,\n\t&-leave-to {\n\t\tmin-width: 0 !important;\n\t\tmax-width: 0 !important;\n\t}\n\t&-enter-to,\n\t&-leave {\n\t\tmin-width: 300px;\n\t\tmax-width: 500px;\n\t}\n\t&-enter-active,\n\t&-leave-active {\n\t\ttransition: $transition;\n\t\ttransition-property: min-width, max-width;\n\t}\n}\n\n@mixin slide-down-rules {\n\t&-enter,\n\t&-leave-to {\n\t\ttransform: translateY(50%);\n\t\topacity: 0;\n\t}\n\t&-enter-to,\n\t&-leave {\n\t\ttransform: translateY(0);\n\t\topacity: 1;\n\t}\n\t&-enter-active,\n\t&-leave-active {\n\t\ttransition: $transition;\n\t\ttransition-property: transform, opacity;\n\t\t/* force top container to resize during animation */\n\t\tposition: absolute !important;\n\t}\n}\n\n@mixin toast-rules {\n\t&-enter,\n\t&-leave-to {\n\t\topacity: 0;\n\t}\n\t&-enter-to,\n\t&-leave {\n\t\topacity: 1;\n\t}\n\t&-enter-active,\n\t&-leave-active {\n\t\ttransition: $transition-slow;\n\t\ttransition-property: opacity;\n\t\ttransition-timing-function: linear;\n\t}\n}\n\n@mixin zoom-rules {\n\t&-enter,\n\t&-leave-to {\n\t\ttransform: scale(0);\n\t}\n\t&-enter-to,\n\t&-leave {\n\t\ttransform: scale(1);\n\t}\n\t&-enter-active,\n\t&-leave-active {\n\t\ttransition: $transition;\n\t\ttransition-property: transform;\n\t}\n}\n\n// Styles block for <transition> component\n.fade {\n\t@include fade-rules;\n}\n\n.radial-reveal {\n\t@include radial-reveal-rules;\n}\n\n.slide-up {\n\t@include slide-up-rules;\n}\n\n.slide-right {\n\t@include slide-right-rules;\n}\n\n.slide-down {\n\t@include slide-down-rules;\n}\n\n.toast {\n\t@include toast-rules;\n}\n\n.zoom {\n\t@include zoom-rules;\n}\n\n// Styles block for for <transition-group> component\n.transition-group {\n\tposition: relative;\n\n\t& > :deep(*) {\n\t\t&.fade {\n\t\t\t@include group-rules;\n\t\t\t@include fade-rules;\n\t\t}\n\t\t&.radial-reveal {\n\t\t\t@include group-rules;\n\t\t\t@include radial-reveal-rules;\n\t\t}\n\t\t&.slide-up {\n\t\t\t@include group-rules;\n\t\t\t@include slide-up-rules;\n\t\t}\n\t\t&.slide-right {\n\t\t\t@include group-rules;\n\t\t\t@include slide-right-rules;\n\t\t}\n\t\t&.slide-down {\n\t\t\t@include group-rules;\n\t\t\t@include slide-down-rules;\n\t\t}\n\t\t&.toast {\n\t\t\t@include group-rules;\n\t\t\t@include toast-rules;\n\t\t}\n\t\t&.zoom {\n\t\t\t@include group-rules;\n\t\t\t@include zoom-rules;\n\t\t}\n\t}\n}\n\n","/**\n * @copyright Copyright (c) 2019 John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @author John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @license AGPL-3.0-or-later\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message utils width\n$message-utils-width: 100px;\n\n$message-input-max-width: calc($messages-list-max-width - 100px);\n\n// background color of call container\n$color-call-background: rgba(34, 34, 34, 0.8);\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
